// importing modules
import React, { Component } from 'react';

//importing components
import Spinner from '../../spinner/Spinner';
import Header from '../../header/Header';
import Layout from '../../layout/Layout';
import SideBar from '../../sidebar/Sidebar';
import DiscoverContent from '../../pages/DiscoverPage/DiscoverContent';

class DiscoverPage extends Component {
    constructor(props){
        super(props);

        this.state = {
            users: [],
            friendsList: [],
            user: {},
            userName: null
        }
        // this.handleLoadUsers = this.handleLoadUsers.bind(this);
        this.handleFriendRequest = this.handleFriendRequest.bind(this);
        this.handleFriends = this.handleFriends.bind(this);
        this.handleDiscoverUser = this.handleDiscoverUser.bind(this);
    }

    componentDidMount(){
        // this.handleLoadUsers();
        this.handleCurrentUser();
        
    }

    handleFriends(){
        fetch('https://cloakdm.herokuapp.com/getFriends', {
            method: 'GET',
            headers: {
                Authorization: 'Bearer ' + this.props.token,
                'Content-Type': 'application/json'
            }
        })
        .then((data) => {
            return data.json();
        })
        .then((usersData) => {
            console.log('friends list', usersData.users);
            this.setState({ friendsList: [...usersData.users] });
        })
        .catch((err) => {
            console.log(err);
        })
    }

    // handleLoadUsers(){
    //     fetch('https://cloakdm.herokuapp.com/users', {
    //         method: 'GET',
    //         headers: {
    //             Authorization: 'Bearer ' + this.props.token, // required to authenticate the user
    //             'Content-Type': 'application/json'
    //         }
    //     })
    //     .then((data) => {
    //         return data.json();
    //     })
    //     .then((usersData) => {
    //         console.log('discover users', usersData.users);
    //         this.setState({ users: [...usersData.users] });
    //     })
    //     .catch((err) => {
    //         console.log(err);
    //     })
    // }

    handleFriendRequest(id){
        fetch(`https://cloakdm.herokuapp.com/sendFriendRequest`, {
            method: 'POST',
            headers: {
                Authorization: 'Bearer ' + this.props.token, // required to authenticate the user
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                id: id
            })
        })
        .then((data) => {
            return data.json();
        })
        .then((data) => {
            console.log(data);
        })
        .catch((err) => {
            console.log(err);
        })
    }

    handleCurrentUser(){
        fetch('https://cloakdm.herokuapp.com/getCurrentUser', {
            method: 'GET',
            headers: {
                Authorization: 'Bearer ' + this.props.token, // required to authenticate the user
                'Content-Type': 'application/json'
            }
        })
        .then((data) => {
            return data.json();
        })
        .then((usersData) => {
            this.setState({ user: {...usersData.user} });
        })
        .catch((err) => {
            console.log(err);
        })
    }

    handleDiscoverUser(e){
        e.preventDefault();
        const userSearch = e.target.value;
        console.log(userSearch);

        fetch(`https://cloakdm.herokuapp.com/discoverUsers`, {
            method: 'POST',
            headers: {
                Authorization: 'Bearer ' + this.props.token, // required to authenticate the user
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                username: userSearch
            })
        })
        .then((data) => {
            return data.json();
        })
        .then((usersData) => {
            console.log(usersData);
            this.setState({ users: [...usersData.users] });
            
            // clear search input if the search bar is clear
            if(userSearch.length === 0 ){
                this.setState({ users: [] });
            }
        })
        .catch((err) => {
            console.log(err);
        })
    }



    render(){
        return (
            <div>
                <Header />
                <Layout>
                    <SideBar user={this.state.user}/>
                    <DiscoverContent 
                        handleDiscoverUser={this.handleDiscoverUser}
                        handleFriendRequest={this.handleFriendRequest}
                        users={this.state.users}/>
                </Layout>
            </div>
        );
    };
}

export default DiscoverPage;